import React from 'react';
import { getServerSession } from "next-auth";
import { authOptions } from "@/lib/auth";
import { redirect } from "next/navigation";
import Link from 'next/link';

export default async function AdminLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const session = await getServerSession(authOptions);

    if (!session) {
        redirect('/admin/login');
    }

    return (
        <div className="flex min-h-screen bg-background text-secondary font-sans">
            {/* Sidebar */}
            <aside className="w-64 border-r border-border p-6 flex flex-col">
                <h2 className="text-2xl font-display text-primary mb-8 tracking-tighter">Admin.</h2>
                <nav className="flex-1 space-y-4">
                    <Link href="/admin" className="block p-2 hover:bg-surfaceHighlight rounded text-sm transition-colors">
                        Dashboard
                    </Link>
                    <Link href="/admin/projects" className="block p-2 hover:bg-surfaceHighlight rounded text-sm transition-colors">
                        Projects
                    </Link>
                    <Link href="/admin/experience" className="block p-2 hover:bg-surfaceHighlight rounded text-sm transition-colors">
                        Experience
                    </Link>
                    <Link href="/admin/skills" className="block p-2 hover:bg-surfaceHighlight rounded text-sm transition-colors opacity-50 cursor-not-allowed">
                        Skills
                    </Link>
                    <Link href="/admin/messages" className="block p-2 hover:bg-surfaceHighlight rounded text-sm transition-colors">
                        Messages
                    </Link>
                    <Link href="/admin/settings" className="block p-2 hover:bg-surfaceHighlight rounded text-sm transition-colors">
                        Settings
                    </Link>
                </nav>
                <div className="pt-6 border-t border-border mt-auto">
                    <div className="text-xs text-secondary/50">
                        Logged in as<br />
                        <span className="text-white">{session.user?.email}</span>
                    </div>
                </div>
            </aside>

            {/* Main Content */}
            <main className="flex-1 p-8 overflow-y-auto">
                {children}
            </main>
        </div>
    );
}
